<template>

    <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog">
            <form @submit.prevent="sendData" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body">
                    <div class="mb-3">
                        <label for="firstName" class="form-label">First Name</label>
                        <input v-model.lazy.trim="form.value.firstName" type="text" class="form-control"
                               id="firstName" required>
                    </div>
                    <div class="mb-3">
                        <label for="lastName" class="form-label">Last Name</label>
                        <input v-model.lazy.trim="form.value.lastName" type="text" class="form-control"
                               id="lastName" required>
                    </div>
                </div>
                <div class="modal-footer">
                    <button ref="cancelBtn" type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                    <button :disabled="loadingSend" type="submit" class="btn btn-primary">
                        <div v-if="loadingData" class="spinner-border spinner-border-sm" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                        Save changes
                    </button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
    import {watch, reactive, ref} from 'vue';
    import axios from "axios";

    export default {
        props: {
            dataSendModal : Object,
            dataGet: Function,
            dataAllGet: Function,
            loading : Boolean,
            postCreator:Boolean,
            notExistColumns:Array,
            checkSearch:String,
            searchFunction:Function
        },
        setup(props,{emit}) {
            const form = reactive({});
            const cancelBtn = ref(null);
            const loadingSend = ref(false);
            const loadingData = ref([]);
            function setData() {
                form.value = props.dataSendModal;
            }
            function loadingChange(){
                loadingData.value = props.loading;
            }
            loadingChange();

            watch(() => props.dataSendModal,() =>{
                setData();
            });
            setData();
            function sendData() {
                if (props.checkSearch === '' || props.checkSearch === undefined) {
                    loadingSend.value = true;
                    loadingData.value = true;
                    if(!props.postCreator){
                        axios.put(`https://retoolapi.dev/6LJzOu/data/${form.value.id}`, {
                            "id": form.value.id,
                            "firstName": form.value.firstName,
                            "lastName": form.value.lastName
                        }).then(function () {
                            cancelBtn.value.click();
                            props.dataAllGet();
                            props.dataGet();
                            emit("btnLoading",loadingData);
                            loadingSend.value = false;
                            loadingData.value = false;
                        })
                    }else{
                        axios.post(`https://retoolapi.dev/6LJzOu/data`, {
                            "id": props.notExistColumns.value[0],
                            "firstName": form.value.firstName,
                            "lastName": form.value.lastName
                        }).then(function () {
                            cancelBtn.value.click();
                            props.dataAllGet();
                            props.dataGet();
                            emit("btnLoading",loadingData);
                            loadingSend.value = false;
                            loadingData.value = false;
                        });
                    }
                }else{
                    loadingSend.value = true;
                    loadingData.value = true;
                    if(!props.postCreator){
                        axios.put(`https://retoolapi.dev/6LJzOu/data/${form.value.id}`, {
                            "id": form.value.id,
                            "firstName": form.value.firstName,
                            "lastName": form.value.lastName
                        }).then(function () {
                            cancelBtn.value.click();
                            emit("btnLoading",loadingData);
                            props.searchFunction()
                            loadingSend.value = false;
                            loadingData.value = false;
                        })
                    }else{
                        axios.post(`https://retoolapi.dev/6LJzOu/data`, {
                            "id": props.notExistColumns.value[0],
                            "firstName": form.value.firstName,
                            "lastName": form.value.lastName
                        }).then(function () {
                            cancelBtn.value.click();
                            props.searchFunction()
                            emit("btnLoading",loadingData);
                            loadingSend.value = false;
                            loadingData.value = false;
                        });
                    }
                }
            }

            return {form,sendData,cancelBtn,loadingSend,loadingData}
        }
    }
</script>

