import {createRouter, createWebHistory} from 'vue-router';
import Home from './pages/Home';
import Slider from './pages/Slider';


import TemplateUser from './pages/users/Form';
import TemplateCart from './pages/users/Cart';

import NotFound from './pages/NotFound';

const routes = [
    { path: '/', name: 'home', component: Home },
    { path: '/slider', name: 'slider', component: Slider },
    { path: '/users', name: 'userTemplate', component: TemplateUser,redirect:{name:'userId',params:{id:1}} , children:[
            // {path: '', name: 'users', component: IndexUser},
            { path: ':id', name: 'userId', component: TemplateCart },
        ] },
    { path: '/:pathMatch(.*)*',name:'notFound', component: NotFound },
]
const router = createRouter({
    history: createWebHistory(),
    routes
});

export default router;