<template>
    <div class="d-flex align-items-center">
        <button @click="modalCreate" type="button" class="btn btn-dark" data-bs-toggle="modal"
                data-bs-target="#exampleModal">Create New Post
        </button>
        <div class="col-xl-3 h-100 px-2">
            <input v-model="searchData" class="form-control form-control-sm h-100" type="text" placeholder="search" aria-label=".form-control-sm example">
        </div>
    </div>
    <div v-if="loading">
        <div class="spinner-border spinner-border-sm" role="status">
            <span class="visually-hidden">Loading...</span>
        </div>
    </div>
    <div class="w-100" v-else>
        <div class="d-flex flex-wrap">
            <div class="col-xl-3 px-2 mb-3" v-for="(data,index) in Datas.value" :key="index">
                <div class="card">
                    <ul class="list-group list-group-flush">
                        <li class="list-group-item">
                            {{ data.firstName }}
                        </li>
                        <li class="list-group-item">
                            {{ data.lastName }}
                        </li>

                    </ul>
                </div>
                <div class="btn-group w-100 mt-2" role="group" aria-label="Basic mixed styles example">
                    <button @click="deleteData(data)" type="button" class="btn btn-danger">Delete</button>
                    <button @click="modalText(data)" type="button" class="btn btn-success" data-bs-toggle="modal"
                            data-bs-target="#exampleModal">Edit
                    </button>
                </div>
            </div>
        </div>

        <div v-if="pagSet">
            <Pagination :check-loading="loadedPage" :pagination-number="paginationNumber" />
        </div>
    </div>
    <div v-if="loadedPage">
        <Modal :search-function="searchInData" :check-search="searchData" @btnLoading="getLoading" :not-exist-columns="res" :post-creator="createPostCheck"
               :data-send-modal="modalData" :loading="loading"
               :data-get="getData" :data-all-get="getAllData"/>
    </div>
</template>

<script>
    import axios from 'axios';
    import {reactive, ref, watch} from "vue";
    import {useRoute,useRouter} from 'vue-router';
    import Swal from "sweetalert2";
    import Modal from "../../components/Modal";
    import Pagination from "../../components/Pagination";

    export default {
        components: {Modal,Pagination},
        setup() {
            const Datas = reactive({});
            const searchData = ref()
            const pagSet = ref(true)
            const route = useRoute();
            const router = useRouter();
            const loading = ref(true);
            const loadedPage = ref(false);
            const modalData = ref([]);
            const paginationNumber = reactive([]);
            const createPostCheck = ref(false);
            const checkRouteChange = ref(false);
            const modalValue = reactive({
                firstName: '',
                lastName: ''
            });
            const res = [];

            function searchInData() {
                loading.value = true;
                loadedPage.value = false;
                axios.get(`https://retoolapi.dev/6LJzOu/data?firstName=${searchData.value}`)
                    .then(function (response) {
                        Datas.value = response.data;
                        pagSet.value = false;
                        if(searchData.value === ""){
                            getAllData()
                            getData();
                            pagSet.value = true;
                        }
                        loading.value = false;
                        loadedPage.value = true;
                    })
                    .catch(function (error) {
                        console.log(error);
                    })
            }

            watch(() => searchData.value,() =>{
                searchInData();
            });

            function getAllData() {
                loading.value = true;
                loadedPage.value = false;
                axios.get(`https://retoolapi.dev/6LJzOu/data`)
                    .then(function (response) {
                        const responseValue = [];
                        const responseIndex = [];
                        response.data.find(function(value,index) {
                            responseValue.push(index+1);
                            responseIndex.push(value.id);
                        });
                        res.value = responseValue.filter(item => !responseIndex.includes(item));
                        paginationNumber.value = Math.ceil(responseValue.length / 12);
                        modalData.value = Object.assign({}, response.data[0]);
                        loading.value = false;
                        loadedPage.value = true;
                    })
                    .catch(function (error) {
                        console.log(error);
                    })
            }
            getAllData();

            function getData() {
                axios.get(`https://retoolapi.dev/6LJzOu/data?_page=${route.params.id}&_limit=12`)
                    .then(function (response) {
                        if (loadedPage.value) {
                            if (paginationNumber.value < parseInt(route.params.id)) {
                                router.push({ name: 'notFound'});
                                // console.log(router);
                            }
                        }
                        if (response.data.length !== 0) {
                            Datas.value = response.data;
                        }else{
                            router.push({ name: 'notFound'});
                        }
                        modalData.value = Object.assign({}, response.data[0]);
                        checkRouteChange.value = true;
                    })
                    .catch(function (error) {
                        console.log(error);
                    })
            }

            getData();


            watch(() => checkRouteChange.value,() =>{
                if (!checkRouteChange.value) {
                    getAllData();
                }
            });
            watch(() => route.params.id,() =>{
                getData();
                if (checkRouteChange.value) {
                    loading.value = true;
                    loadedPage.value = false;
                    checkRouteChange.value = false;
                }
            });
            function modalCreate() {
                modalData.value = Object.assign({}, modalValue);
                createPostCheck.value = true;
            }

            function modalText(value) {
                modalData.value = Object.assign({}, value);
                createPostCheck.value = false;
            }

            function deleteData(value) {
                Swal.fire({
                    title: 'Are you sure?',
                    text: "You won't be able to revert this!",
                    icon: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: 'Yes, delete it!'
                }).then((result) => {
                    if (result.isConfirmed) {
                        axios.delete(`https://retoolapi.dev/6LJzOu/data/${value.id}`,).then(function () {
                            Swal.fire(
                                'Deleted!',
                                'Your file has been deleted.',
                                'success'
                            )
                            loading.value = false;

                            if (searchData.value === '' || searchData.value === undefined) {
                                if (Datas.value.length <= 1) {
                                    router.push({name: 'userId', params: {id: paginationNumber.value - 1}})
                                } else {
                                    getAllData()
                                    getData()
                                }
                            }else{
                                searchInData()
                            }

                        })
                    }
                })
            }

            function getLoading(e) {
                if (e !== undefined) {
                    loading.value = e.value
                }
            }

            getLoading()

            return {
                Datas,
                loading,
                deleteData,
                modalText,
                loadedPage,
                getLoading,
                modalCreate,
                res,
                createPostCheck,
                getData,
                getAllData,
                modalData,
                paginationNumber,
                searchData,
                pagSet,
                searchInData
            }
        }
    }
</script>

