<template>
    <div>
        <h1>slider dude</h1>
        <Flickity ref="flickity" :options="flickityOptions">
            <div class="carousel-cell w-50 text-center">111111111111111111111111111111111111111111111111111</div>
            <div class="carousel-cell w-50 text-center">111111111111111111111111111111111111111111111111111</div>
            <div class="carousel-cell w-50 text-center">111111111111111111111111111111111111111111111111111</div>
            <div class="carousel-cell w-50 text-center">111111111111111111111111111111111111111111111111111</div>
            <div class="carousel-cell w-50 text-center">111111111111111111111111111111111111111111111111111</div>
            <div class="carousel-cell w-50 text-center">111111111111111111111111111111111111111111111111111</div>
            <div class="carousel-cell w-50 text-center">111111111111111111111111111111111111111111111111111</div>
        </Flickity>
    </div>
    <button class="mt-5" @click="next()">Custom Next Button</button>
</template>

<script>
    import Flickity from 'vue-flickity';
    export default {
        components: {
            Flickity
        },
        data() {
            return {
                flickityOptions: {
                    initialIndex: 3,
                    prevNextButtons: false,
                    pageDots: false,
                    wrapAround: true,
                    cellAlign: 'left',
                    contain: true

                    // any options from Flickity can be used
                }
            }
        },
        methods: {
            next() {
                this.$refs.flickity.next();
            },
        }
    }
</script>

<style scoped>
    .carousel-cell{
        border: 1px solid #000;
    }
</style>
