<template>
    <nav aria-label="Page navigation example" class="pt-4" v-if="checkLoading">
        <ul class="pagination justify-content-center pagination-sm">
            <li class="page-item" :class="{disabled : checkPrevValue}">
                <router-link class="page-link" :to="{name:'userId',params:{id:1}}"> ((</router-link>
            </li>
            <li class="page-item" :class="{disabled : checkPrevValue}">
                <router-link class="page-link" :to="{name:'userId',params:{id:prevValue}}">Previous</router-link>
            </li>

            <li class="page-item" :class="{active: checkFirstNum}">
                <router-link class="page-link" :to="{name:'userId',params:{id:1}}">{{ 1 }}</router-link>
            </li>
            <li class="page-item" v-if="getNumbers.prevActNumber >= 4">
                <span class="page-link">...</span>
            </li>
            <li class="page-item" v-if="getNumbers.prevSecondActNumber > 1">
                <router-link class="page-link" :to="{name:'userId',params:{id:getNumbers.prevSecondActNumber}}">
                    {{getNumbers.prevSecondActNumber}}
                </router-link>
            </li>
            <li class="page-item" v-if="getNumbers.prevActNumber > 1">
                <router-link class="page-link" :to="{name:'userId',params:{id:getNumbers.prevActNumber}}">
                    {{getNumbers.prevActNumber}}
                </router-link>
            </li>
            <li class="page-item active" v-if="getNumbers.Active !== 1 && getNumbers.Active !== paginationNumber.value">
                <router-link class="page-link" :to="{name:'userId',params:{id:getNumbers.Active}}">
                    {{getNumbers.Active}}
                </router-link>
            </li>
            <li class="page-item" v-if="getNumbers.NextActNumber < paginationNumber.value">
                <router-link class="page-link" :to="{name:'userId',params:{id:getNumbers.NextActNumber}}">
                    {{getNumbers.NextActNumber}}
                </router-link>
            </li>
            <li class="page-item" v-if="getNumbers.NextSecondActNumber < paginationNumber.value">
                <router-link class="page-link" :to="{name:'userId',params:{id:getNumbers.NextSecondActNumber}}">
                    {{getNumbers.NextSecondActNumber}}
                </router-link>
            </li>
            <li class="page-item" v-if="getNumbers.NextActNumber <= paginationNumber.value - 3">
                <span class="page-link">...</span>
            </li>
            <li class="page-item" :class="{active: checkLastNum}">
                <router-link class="page-link" :to="{name:'userId',params:{id:paginationNumber.value}}">
                    {{paginationNumber.value}}
                </router-link>
            </li>

            <li class="page-item" :class="{disabled : checkNextValue}">
                <router-link class="page-link" :to="{name:'userId',params:{id:nextValue}}">Next</router-link>
            </li>

            <li class="page-item" :class="{disabled : checkNextValue}">
                <router-link class="page-link" :to="{name:'userId',params:{id:paginationNumber.value}}"> ))
                </router-link>
            </li>
        </ul>
    </nav>
    <div v-else>
        not Loaded
    </div>
</template>

<script>
    import {ref} from 'vue';
    import {useRoute} from 'vue-router';

    export default {
        props: {
            paginationNumber: Array,
            checkLoading: Boolean
        },
        setup(props) {
            const route = useRoute();
            const nextValue = ref();
            const checkNextValue = ref(false);
            const prevValue = ref();
            const checkPrevValue = ref(false);
            const getNumbers = ref({
                prevSecondActNumber: 0,
                prevActNumber: 0,
                Active: 0,
                NextActNumber: 0,
                NextSecondActNumber: 0,
            });
            const checkFirstNum = ref(false);
            const checkLastNum = ref(false);

            function getindex() {
                if (parseInt(route.params.id) - 1 <= 1) {
                    getNumbers.value.prevSecondActNumber = 0
                } else {
                    getNumbers.value.prevSecondActNumber = parseInt(route.params.id) - 2
                }

                if (parseInt(route.params.id) - 1 <= 1) {
                    getNumbers.value.prevActNumber = 0
                } else {
                    getNumbers.value.prevActNumber = parseInt(route.params.id) - 1
                }
                getNumbers.value.Active = parseInt(route.params.id);

                if (parseInt(route.params.id) + 1 <= props.paginationNumber.value) {
                    getNumbers.value.NextActNumber = parseInt(route.params.id) + 1
                } else {
                    getNumbers.value.NextActNumber = props.paginationNumber.value
                }
                if (parseInt(route.params.id) + 1 <= props.paginationNumber.value) {
                    getNumbers.value.NextSecondActNumber = parseInt(route.params.id) + 2
                } else {
                    getNumbers.value.NextSecondActNumber = props.paginationNumber.value
                }
                if (parseInt(route.params.id) === props.paginationNumber.value) {
                    checkLastNum.value = true
                } else {
                    checkLastNum.value = false
                }
                if (parseInt(route.params.id) === 1) {
                    checkFirstNum.value = true
                } else {
                    checkFirstNum.value = false
                }
            }

            function prevPosition() {
                prevValue.value = parseInt(route.params.id) - 1
            }

            function checkPrev() {
                if (prevValue.value === 0) {
                    checkPrevValue.value = true
                }
            }

            function nextPosition() {
                nextValue.value = parseInt(route.params.id) + 1
            }

            function checkNext() {
                if (props.paginationNumber.value < nextValue.value) {
                    checkNextValue.value = true
                }
            }

            if (props.checkLoading) {
                getindex()
                prevPosition()
                checkPrev()
                nextPosition()
                checkNext()
            }


            return {prevValue, checkPrevValue, nextValue, checkNextValue, getNumbers, checkFirstNum, checkLastNum}
        }
    }
</script>

<style scoped>

</style>
